<template>
    <div class="container">
      <nav-bar title="订单详情" :border="false" ></nav-bar>
      <div class="top-box">
        <div class="label-box">
            <img :src="iconUrl" alt="" class="icon">
            <div class="text">{{ order.state_text }}</div>
        </div>
      </div>
      <div class="card-wrap">
        <div class="card card1">
            <div class="text1">{{ address.name + ' ' +  address.phone}}</div>
            <div class="text2" v-if="address.region">{{ address.region.province + address.region.city + address.region.region + address.detail }}</div>
        </div>
        <div class="card card2">
            <div class="item" v-for="item in order.goods" :key="item.goods_id" @click="$router.push(`/goods/${item.goods_id}`)">
               <div class="left">
                  <img :src="item.goods_image" alt="">
                  <div class="text line2">{{ item.goods_name }}</div>
               </div>
               <div class="right">
                  <div class="price">￥{{ item.goods_price }}</div>
                  <div class="num">x {{ item.total_num }}</div>
               </div>
            </div>
        </div>
        <div class="card card3">
            <div class="item">
                <div class="label">订单编号</div>
                <div class="text">{{ order.order_no }}</div>
                <div class="copy-btn" @click="copyText">复制</div>
            </div>
            <div class="item">
                <div class="label">下单时间</div>
                <div class="text">{{ order.create_time }}</div>
            </div>
            <div class="item">
                <div class="label">买家留言</div>
                <div class="text">{{ order.buyer_remark || '--' }}</div>
            </div>
        </div>
        <div class="card card4">
            <div class="item-between">
                <div class="label">订单金额</div>
                <div class="text">￥{{ order.total_price }}</div>
            </div>
            <div class="item-between" v-if="order.coupon_id!==0">
                <div class="label">优惠券抵扣</div>
                <div class="text">- ￥{{ order.coupon_money }}</div>
            </div>
            <div class="item-between">
                <div class="label">运费</div>
                <div class="text">+ ￥0.00</div>
            </div>
            <div class="total-price">实付款 <span>￥{{ order.pay_price }}</span></div>
        </div>
      </div>
      <div class="btn-box">
        <button class="btn" @click="cancelOrder(order.order_id)" v-if="order.state_text==='待发货'">申请取消</button>
        <div class="text" v-if="order.state_text==='待取消'">取消申请中</div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'OrderDetail',
  data () {
    return {
      order: {},
      address: {},
      update: false
    }
  },
  created () {
    this.getOrderDetail()
  },
  computed: {
    orderId () {
      return this.$route.params.id || ''
    },
    iconUrl () {
      if (this.order) {
        const state = this.order.state_text
        if (state === '待发货') {
          return require('@/assets/images/wait_deliver.png')
        }
        if (state === '待取消') {
          return require('@/assets/images/close.png')
        }
      }
      return ''
    }
  },
  methods: {
    async getOrderDetail () {
      const { data } = await this.$api.order.getOrderDetail(this.orderId)
      // data.order.total_price = (data.order.goods.reduce((sum, i) => sum + i.line_price * i.total_num, 0)).toFixed(2)
      this.order = data.order
      this.address = data.order.address
    },
    cancelOrder (orderId) {
      this.$dialog.confirm({
        message: '确定要取消该订单吗？'
      })
        .then(async () => {
          await this.$api.order.cancelOrder(orderId)
          this.getOrderDetail().then(() => {
            this.update = true
            this.$toast.success('订单取消成功')
          })
        })
        .catch(() => {})
    },
    copyText () {
      const text = this.order.order_no
      this.$copyText(text).then(
        e => {
          this.$toast('复制成功')
        },
        e => {
          this.$toast('复制失败：' + e)
        }
      )
    }
  },
  // 离开页面执行操作
  beforeRouteLeave (to, from, next) {
    if (to.name === 'Order' && this.update) {
      to.query.update = true
    } else {
      to.query.update = false
    }
    next()
  }
}
</script>

<style lang="less" scoped>
.container{
    background-color: @primaryBg;
    min-height: 100vh;
    padding-bottom: 50px;
}
/deep/ .van-nav-bar{
   background-color: #E8C269;
   .van-nav-bar__title,.van-nav-bar__arrow{
      color: #fff;
   }
}
.top-box{
   background-color: #E8C269;
   padding: 30px 15px 50px;
   .label-box{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    .icon{
        width: 70px;
        height: 70px;
    }
    .text{
        font-size: 20px;
        font-weight: bold;
        color: #fff;
        margin-left: 15px;
    }
   }
}
.card-wrap{
    padding: 0 15px;
    margin-top: -25px;
    .card{
        background-color: #fff;
        border-radius: 5px;
        margin-bottom: 10px;
        box-shadow: 0 0.5px 2px 0 rgba(0,0,0,.05);
        padding: 12px;
        font-size: 14px;
    }
    .card1{
        .text2{
            font-size: 13px;
            margin-top: 6px;
            color: #999;
        }
    }
    .card2{
        .item{
          display: flex;
          align-items: flex-start;
          justify-content: space-between;
          margin-bottom: 10px;
          &:last-of-type{
            margin-bottom: 0;
          }
            .left{
                display: flex;
                align-items: flex-start;
                justify-content: flex-start;
                width: 70%;
                img{
                    width: 80px;
                    height: 80px;
                    border-radius: 5px;
                }
                .text{
                    flex: 1;
                    margin-left: 10px;
                }
            }
            .right{
                color: #999;
                text-align: right;
                font-size: 13px;
                .num{
                    margin-top: 8px;
                    font-size: 12px;
                }
            }
        }
    }
    .card3{
        .item{
            display: flex;
            align-items: center;
            justify-content: flex-start;
            margin-bottom: 8px;
            .label{
                color: #999;
            }
            .text{
                margin-left: 15px;
            }
            &:last-of-type{
                margin-bottom: 0;
            }
            .copy-btn{
                border: 1px solid #999;
                border-radius: 20px;
                color: #999;
                font-size: 12px;
                padding:0 8px;
                margin-left: 10px;
            }
            &:nth-of-type(3){
                align-items: flex-start;
                .text{
                    width: 240px;
                    word-wrap: break-word;
                    word-break: normal;
                }
            }
        }
    }
    .card4{
        .item-between{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 10px;
            .label{
                color: #999;
            }
        }
        .total-price{
            text-align: right;
            border-top: 1px solid #f0efef;
            padding-top: 10px;
            font-size: 15px;
            span{
                color: red;
            }
        }
    }
}
.btn-box{
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    z-index: 10;
    height: 50px;
    line-height: 50px;
    background-color: #fff;
    box-shadow: 0 -2px 20px 0 hsla(0,0%,59.2%,.24);
    text-align: right;
    padding: 0 15px;
    .btn{
        height: 30px;
        line-height: 30px;
        padding: 0 15px;
        border: 1px solid #a8a8a8;
        border-radius: 20px;
        font-size: 15px;
    }
    .text{
        color: #b9b9b9;
    }
}
</style>
